.TwoFactorActivate {
  width: 100%;
  height: 100%;
  position: relative;
  display: grid;
  justify-items: center;
}

/*.TwoFactorActivate.success{
  opacity: 0;
}*/

.TwoFactorActivate.desaparecer {
  -webkit-transform: translateY(30px);
          transform: translateY(30px);
}

.TLayout {
  width: 100%;
  height: 100%;
  max-width: 800px;
  position: absolute;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 25% 1fr;
  -webkit-transition: 0.3s;
  transition: 0.3s;
}

.layer1 {
  z-index: 1;
}

.layer2 {
  z-index: 2;
}

.header2fa {
  display: grid;
}

.header2fa h3 {
  text-align: center;
  color: #00bfff;
  font-size: 25px;
}

.body2fa {
  height: 100%;
  /*background: white;*/
}

.bodySon {
  width: 100%;
  height: 50%;
  -webkit-transition: 0.32s;
  transition: 0.32s;
  display: grid;
  align-items: center;
  justify-items: center;
}

.footer2fa {
  width: 100%;
  min-height: 50%;
  height: auto;
  /*display: grid;*/
  position: relative;
}

.footer2fa .AuthInputComp {
  justify-self: center;
}

.footer2faText {
  position: relative;
  height: 100px;
  display: grid;
}

.footer2faTextDes span {
  font-size: 22px !important;
}

.footer2faTextDes p {
  width: 80%;
  max-width: 650px;
  font-size: 16px;
}

.footer2faTextDes {
  position: absolute;
  overflow: hidden;
  -webkit-transition: 0.3s;
  transition: 0.3s;
  height: 0;
  width: 100%;
  display: grid;
  align-items: center;
  justify-items: center;
}

.footer2faTextDes.desp {
  -webkit-animation-name: despAnim;
          animation-name: despAnim;
  -webkit-animation-duration: 0.4s;
          animation-duration: 0.4s;
  /*animation-delay: 1s; */
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-timing-function: cubic-bezier(1, 1, 1, 1);
          animation-timing-function: cubic-bezier(1, 1, 1, 1);
}

@-webkit-keyframes despAnim {
  0% {
    height: 0;
  }
  60% {
    height: 0;
  }
  100% {
    height: 110px;
  }
}

@keyframes despAnim {
  0% {
    height: 0;
  }
  60% {
    height: 0;
  }
  100% {
    height: 110px;
  }
}

.footer2faTextDes.pleg {
  -webkit-animation-name: despAnim;
          animation-name: despAnim;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  animation-direction: reverse;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-timing-function: cubic-bezier(1, 1, 1, 1);
          animation-timing-function: cubic-bezier(1, 1, 1, 1);
}

.header2fa .iconSty {
  justify-content: center !important;
}

.TwoFactorActivate p {
  margin: 0;
  color: gray;
  text-align: center;
}

.secretCode {
  color: #1babec !important;
  text-decoration: underline;
  height: 80px;
  font-size: 25px;
  margin-top: 10px !important;
}

.footer2fa #authReq {
  position: relative !important;
  margin-top: 20px;
}

